'use client';

import { useState, useMemo } from 'react';
import type { Student } from '@/lib/types';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogDescription,
} from '@/components/ui/dialog';
import { PlusCircle, Search, Pencil } from 'lucide-react';
import { StudentForm } from './StudentForm';
import { Card, CardContent } from './ui/card';
import { useToast } from '@/hooks/use-toast';
import { Skeleton } from './ui/skeleton';

type StudentListProps = {
  initialStudents: Student[];
  initialSexos: { id: string; nombre: string }[];
  initialCourses: { id: string; nombre: string }[];
};

export function StudentList({ initialStudents, initialSexos, initialCourses }: StudentListProps) {
  const [students, setStudents] = useState<Student[]>(initialStudents);
  const [sexos, setSexos] = useState<{ id: string; nombre: string }[]>(initialSexos);
  const [courses, setCourses] = useState<{ id: string; nombre: string }[]>(initialCourses);
  const [isLoading, setIsLoading] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);
  const { toast } = useToast();

  const filteredStudents = useMemo(() =>
    students.filter(student =>
      `${student.nombres} ${student.apellidos}`.toLowerCase().includes(searchTerm.toLowerCase()) ||
      student.id.toLowerCase().includes(searchTerm.toLowerCase()) ||
      student.grade.toLowerCase().includes(searchTerm.toLowerCase())
    ), [students, searchTerm]);

  const maxId = useMemo(() => {
    if (students.length === 0) {
      return 0;
    }
    const max = Math.max(...students.map(s => parseInt(s.id, 10)));
    return isFinite(max) ? max : 0;
  }, [students]);

  const handleAddStudent = (newStudent: Student) => {
    setStudents(prev => {
      const updatedStudents = [...prev, newStudent];
      return updatedStudents.sort((a, b) => parseInt(a.id, 10) - parseInt(b.id, 10));
    });
    setIsFormOpen(false);
  };

  const handleEditClick = (student: Student) => {
    setSelectedStudent(student);
    setIsEditModalOpen(true);
  };

  const handleStudentUpdated = (updatedStudent: Student) => {
    setStudents(prev => prev.map(s => s.id === updatedStudent.id ? updatedStudent : s));
    setIsEditModalOpen(false);
  };

  const TableSkeleton = () => (
    <Table>
      <TableHeader>
        <TableRow key="skeleton-header">
          {[...Array(10)].map((_, i) => <TableHead key={i}><Skeleton className="h-5 w-full" /></TableHead>)}
        </TableRow>
      </TableHeader>
      <TableBody>
        {[...Array(5)].map((_, i) => (
          <TableRow key={i}>
            <TableCell colSpan={10}>
              <Skeleton className="h-8 w-full" />
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );

  return (
    <>
      <Dialog open={isFormOpen} onOpenChange={setIsFormOpen}>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between gap-4 mb-4">
              <div className="relative w-full max-w-sm">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Buscar por nombre, ID, grado..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10"
                />
              </div>
              <DialogTrigger asChild>
                <Button>
                  <PlusCircle className="mr-2 h-4 w-4" />
                  Matricular Estudiante
                </Button>
              </DialogTrigger>
            </div>
            <div className="rounded-lg border">
              {students.length === 0 && !isLoading ? (
                <div className="h-24 text-center flex items-center justify-center">
                  No se encontraron estudiantes.
                </div>
              ) : isLoading ? (
                <TableSkeleton />
              ) : (
                <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Nº Registro</TableHead>
                    <TableHead>RUT</TableHead>
                    <TableHead>Nombre</TableHead>
                    <TableHead className="hidden sm:table-cell">Sexo</TableHead>
                    <TableHead className="hidden sm:table-cell">Fec. Nacimiento</TableHead>
                    <TableHead className="hidden sm:table-cell">Curso</TableHead>
                    <TableHead className="hidden sm:table-cell">Fec. Matrícula</TableHead>
                    <TableHead className="hidden sm:table-cell">Fec. Retiro</TableHead>
                    <TableHead className="text-right">Acciones</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredStudents.length > 0 ? (
                    filteredStudents.map((student) => (
                      <TableRow key={student.id}>
                        <TableCell className="font-medium text-xs whitespace-nowrap overflow-hidden text-ellipsis">
                          {student.id}
                        </TableCell>
                        <TableCell className="text-xs whitespace-nowrap overflow-hidden text-ellipsis">{student.rut}</TableCell>
                        <TableCell className="text-xs whitespace-nowrap overflow-hidden text-ellipsis">
                          <div className="flex items-center gap-2">
                            {`${student.nombres} ${student.apellidos}`}
                          </div>
                        </TableCell>
                        <TableCell className="hidden sm:table-cell text-xs whitespace-nowrap overflow-hidden text-ellipsis">{student.sexo?.nombre}</TableCell>
                        <TableCell className="hidden sm:table-cell text-xs whitespace-nowrap overflow-hidden text-ellipsis">{new Date(student.fecha_nacimiento).toLocaleDateString('es-ES', { timeZone: 'UTC' })}</TableCell>
                        <TableCell className="hidden sm:table-cell text-sm whitespace-nowrap overflow-hidden text-ellipsis">{student.grade}</TableCell>
                        <TableCell className="hidden sm:table-cell text-xs whitespace-nowrap overflow-hidden text-ellipsis">{new Date(student.enrollmentDate).toLocaleDateString('es-ES', { timeZone: 'UTC' })}</TableCell>
                        <TableCell className="hidden sm:table-cell text-xs whitespace-nowrap overflow-hidden text-ellipsis">{student.fecha_retiro ? new Date(student.fecha_retiro).toLocaleDateString('es-ES', { timeZone: 'UTC' }) : '-'}</TableCell>
                        <TableCell className="text-right">
                          <Button variant="ghost" size="icon" onClick={() => handleEditClick(student)}><Pencil className="h-4 w-4" /></Button>
                        </TableCell>
                      </TableRow>
                    ))
                  ) : (
                    <TableRow>
                      <TableCell colSpan={10} className="h-24 text-center">
                        No se encontraron estudiantes.
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
              )}
            </div>
          </CardContent>
        </Card>
        <DialogContent className="sm:max-w-2xl">
          <DialogHeader>
            <DialogTitle className="font-headline">Matricular Nuevo Estudiante</DialogTitle>
            <DialogDescription>
              Complete los detalles a continuación para registrar a un nuevo estudiante. Se generará una foto de perfil por IA si no se carga una.
            </DialogDescription>
          </DialogHeader>
          <StudentForm onSuccess={handleAddStudent} nextId={maxId + 1} sexos={sexos} courses={courses} />
        </DialogContent>
      </Dialog>

      {selectedStudent && (
        <Dialog open={isEditModalOpen} onOpenChange={setIsEditModalOpen}>
          <DialogContent className="sm:max-w-2xl">
            <DialogHeader>
              <DialogTitle className="font-headline">Editar Estudiante</DialogTitle>
              <DialogDescription>
                Actualice los datos del estudiante.
              </DialogDescription>
            </DialogHeader>
            <StudentForm
              student={selectedStudent}
              onSuccess={handleStudentUpdated}
              sexos={sexos}
              courses={courses}
            />
          </DialogContent>
        </Dialog>
      )}
    </>
  );
}
